<script >
export default {
  data () {
    return {
      jsonList:[],
      list:[{
        value: '2',
        label: '黄金糕'
      }, {
        value: '4',
        label: '双皮奶'
      }, {
        value: '3',
        label: '蚵仔煎'
      }, {
        value: '5',
        label: '龙须面'
      }, {
        value: '6',
        label: '北京烤鸭'
      }],
      loading: false
    }
  },
  created () {
    //
    this.findList()
  },
  methods: {
    findList () {
      this.loading = true
      return new Promise(resolve => {
        setTimeout(() => {
          const data = this.mockList(1500)
          // 阻断 vue 对大数组的监听，避免 vue 绑定大数据造成短暂的卡顿
          if (this.$refs.xTable) {
            this.$refs.xTable.loadData(data)
          }
          resolve()
          this.loading = false
        }, 300)
      })
    },
    mockList (size) {
      const list = []
      for (let index = 0; index < size; index++) {
        list.push({
          name: ``,
          sex: '',
          num: '',
          age: '',
          num2: '',
          val1:'',
          rate: '',
          address: ''
        })
      }
      this.jsonList=list
      return list
    },
  }
}

</script>

<template>
 <div>
    当前数据总条数{{jsonList.length}}条
   <vxe-table
       border
       resizable
       show-overflow
       keep-source
       ref="xTable"
       height="400"
       :export-config="{}"
       :loading="loading"
       :edit-config="{trigger: `click`, mode: 'cell'}"
       :scroll-y="{enabled: true}">
     <vxe-column field="name" title="但是" width="100" >
       <template #default="{ row }">
         <el-select class="testceshi" v-model="row.name" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
     <vxe-column field="age" title="Age" width="200" >
       <template #default="{ row }">
         <el-date-picker class="age"
             v-model="row.age"
             type="date"
             placeholder="选择日期">
         </el-date-picker>
       </template>
     </vxe-column>
     <vxe-column field="sex" title="Sex" width="200" >
       <template #default="{ row }">
         <el-checkbox class="sex" v-model="row.sex" placeholder="请选择"></el-checkbox>
       </template>
     </vxe-column>
     <vxe-column field="rate" title="Rate" width="200"  >
       <template #default="{ row }">
         <el-switch class="rate" v-model="row.rate" placeholder="请选择"></el-switch>
       </template>
     </vxe-column>
     <vxe-column field="region" title="Region" width="200"  >
       <template #default="{ row }">
         <el-select class="region" v-model="row.region" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
     <vxe-column field="time" title="Time" width="200"  >
       <template #default="{ row }">
         <el-select class="Time" v-model="row.time" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
     <vxe-column field="address" title="Address" width="300" show-overflow  >
       <template #default="{ row }">
         <el-select class="address" v-model="row.address" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
     <vxe-column field="updateTime" title="UpdateTime" width="200"  >
       <template #default="{ row }">
         <el-select class="updateTime" v-model="row.updateTime" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
     <vxe-column field="attr1" title="Attr1" width="200"  >
       <template #default="{ row }">
         <el-select class="attr1" v-model="row.attr1" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
     <vxe-column field="attr2" title="Attr2" width="200"  >
       <template #default="{ row }">
         <el-select class="attr2" v-model="row.attr2" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
     <vxe-column field="attr3" title="Attr3" width="200"  >
       <template #default="{ row }">
         <el-select class="attr3" v-model="row.attr3" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
     <vxe-column field="attr4" title="Attr4" width="200"  >
       <template #default="{ row }">
         <el-select class="attr4" v-model="row.attr4" placeholder="请选择">
           <el-option
               v-for="item in list"
               :key="item.value"
               :label="item.label"
               :value="item.value">
           </el-option>
         </el-select>
       </template>
     </vxe-column>
   </vxe-table>
 </div>
</template>

<style  lang="less">
.vxe-select--panel.is--transfer{
  position: absolute !important;
  width: 100%;
  left: 0 !important;
  top: 34px !important;
}
</style>